@import "theme";

.container.welcome {
  display: flex;
  justify-content: space-between;
  padding-top: 3rem;
  padding-bottom: 1rem;
  height: 450px;

  #narrow-box {
    height: 100%;
    width: 30%;
    min-width: min-content;
    background-color: $header-background;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h1 {
      color: $main-text;
      margin-bottom: 30px;

      &::after {
        background-color: $main-text;
      }
    }

    p {
      color: $main-text;
    }

    button {
      @include rect-btn;
      @include blue-btn;
      align-self: flex-end;
      margin-top: 2rem;
    }
  }

  #wide-box {
    height: 100%;
    width: 65%;
    min-width: auto;
    background-color: $color-gray;
    overflow-y: auto;
    overflow-x: hidden;

    img {
      min-height: 150px;
      min-width: 540px;
    }

    .content {
      padding: 10px 40px;
    }

    h2,
    p {
      color: $secondary-text;
    }
  }
}

@media only screen and (max-width: 690px) {
  .container.welcome {
    display: block;
    padding-top: 0;
    padding-bottom: 1rem;
    height: auto;

    #narrow-box {
      height: auto;
      width: 100%;
      min-width: min-content;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    #wide-box {
      width: auto;
      height: auto;
      margin-top: 1rem;
    }
  }
}
