@import "theme";

$footer-bgcolor: rgb(29, 32, 34);

footer {
  background-color: $footer-bgcolor;
  color: $main-text;
}

.container.footer {
  display: flex;
  justify-content: space-between;
  max-width: 1080px;
  padding: 20px 80px 20px 10px;

  p#footer-text {
    opacity: 0.6;
    font-size: 1rem;
    width: 45%;
  }

  .footer-right {
    text-align: right;
  }

  #footer-menu {
    margin-bottom: 20px;
  }
  ul {
    li {
      button {
        @include nav-btn;
      }
    }
  }
}

@media only screen and (max-width: 690px) {
  .container.footer {
    justify-content: center;
    padding: 15px;

    p#footer-text,
    #footer-menu {
      display: none;
    }
  }
}
